import React, { useContext, useEffect, useRef, useState } from "react";
import { jiraContext } from "../context/jiraContext";
import { v4 as uuidv4 } from "uuid";

const JiraForm = () => {
  const {
    formData,
    setFormData,
    formInitialState,
    jiraData,
    setJiraData,
    setShow,
    formToggle,
    setFormToggle
  } = useContext(jiraContext);
  const ref = useRef();

  useEffect(() => {
    ref.current.style.display = "none";
    localStorage.setItem("jira", JSON.stringify(jiraData));
  }, [jiraData]);

  const handleChange = (event) => {
    const { value, name } = event.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleDisplay = () => {
    setFormToggle(!formToggle);
    if (formToggle) {
      ref.current.style.display = "none";
    } else {
      ref.current.style.display = "block";
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (formData.updateId) {
      const updateData = jiraData.map((d) => {
        if (d.id === formData.updateId) {
          return { ...d, ...formData };
        }
        return d;
      });
      setJiraData(updateData);
      setFormData(formInitialState);
    } else {
      const data = {
        id: uuidv4(),
        ...formData
      };
      setJiraData([...jiraData, data]);
      setFormData(formInitialState);
      setShow(false);
    }
  };

  return (
    <React.Fragment>
      <div className="issues-text">
        <h3>
          {" "}
          <b style={{ fontSize: "30px" }}>{jiraData.length}</b> issues are on
          tracking
        </h3>
      </div>
      <div class="form-main-first">
        <div>
          <button onClick={handleDisplay}>
            {formToggle ? "Close Form" : "Open Form"}
          </button>
        </div>
        <div ref={ref} className="dropdown-menu">
          <form>
            <input
              type="text"
              placeholder="Enter the ticket details"
              name="ticket"
              value={formData.ticket}
              onChange={handleChange}
            />
            <select
              value={formData.prority}
              name="prority"
              onChange={handleChange}
            >
              <option value=""></option>
              <option value="critical">critical</option>
              <option value="major">major</option>
              <option value="minor">minor</option>
            </select>
            {/* <input
              type="text"
              placeholder="Enter the priority"
              name="prority"
              value={formData.prority}
              onChange={handleChange}
            /> */}
            <input
              type="date"
              placeholder="Enter the date"
              name="date"
              value={formData.date}
              onChange={handleChange}
            />
            <input
              type="text"
              placeholder="Enter the reporter name"
              name="reporter"
              value={formData.reporter}
              onChange={handleChange}
            />
            <textarea
              placeholder="Enter the remarks"
              name="remarks"
              value={formData.remarks}
              onChange={handleChange}
              rows="10"
              cols="41"
            />
            <input
              onClick={handleSubmit}
              type="submit"
              value={formData.updateId ? "Update" : "Submit"}
            />
          </form>
        </div>
      </div>
    </React.Fragment>
  );
};
export default JiraForm;
