import React, { useContext } from "react";
import { jiraContext } from "../context/jiraContext";

const JiraTicket = () => {
  const { setFillData, jiraData } = useContext(jiraContext);
  const issueArr = ["issues", "critical", "major", "minor"];

  const handleIssue = (issueType) => {
    let data;
    switch (issueType) {
      case "critical":
        data = jiraData.filter((d) => d.prority === issueType);
        break;
      case "major":
        data = jiraData.filter((d) => d.prority === issueType);
        break;
      case "minor":
        data = jiraData.filter((d) => d.prority === issueType);
        break;
      default:
        data = jiraData;
        break;
    }
    setFillData(data);
  };

  return (
    <React.Fragment>
      <div className="issue-btn">
        {issueArr &&
          issueArr.map((d) => {
            return (
              <button
                style={{
                  background: `${
                    (d === "critical" && "red") ||
                    (d === "major" && "orange") ||
                    (d === "minor" && "green") ||
                    (d === "issues" && "Blue")
                  }`
                }}
                onClick={() => handleIssue(d)}
              >
                {d}
              </button>
            );
          })}
      </div>
    </React.Fragment>
  );
};

export default JiraTicket;
