import React, { useContext } from "react";
import { FaCloudMoon, FaSun } from "react-icons/fa";
import { jiraContext } from "../context/jiraContext";

const DisplayMode = () => {
  const { mode, setMode, modeRef } = useContext(jiraContext);

  const handleMode = () => {
    setMode(!mode);
    if (mode) {
      modeRef.current.style = `
      background-color: white;
      color: black;
    `;
    } else {
      modeRef.current.style = `
      background-color: #121212;
      color: white;
      `;
    }
  };

  return (
    <React.Fragment>
      <span className="font-icons" onClick={handleMode}>
        {mode ? <FaCloudMoon /> : <FaSun color="orange" />}
      </span>
    </React.Fragment>
  );
};

export default DisplayMode;
