import React, { useContext, useEffect, useState } from "react";
import moment from "moment";
import { jiraContext } from "../context/jiraContext";
import Loadmore from "./Loadmore";

const JiraDisplay = () => {
  const {
    jiraData,
    setJiraData,
    setFormData,
    fillData,
    show,
    setShow
  } = useContext(jiraContext);
  const [count, setCount] = useState(6);

  useEffect(() => {
    if (fillData.length) {
      setShow(true);
    } else {
      setShow(false);
    }
  }, [fillData]);

  const handleDelete = (id) => {
    const findData = jiraData.filter((d) => d.id !== id);
    if (window.confirm("Are you sure to delete issue")) {
      setJiraData(findData);
    }
  };

  const handleUpdate = (id) => {
    const findUpdate = jiraData.find((d) => d.id === id);
    const data = {
      updateId: id,
      ...findUpdate
    };
    setFormData(data);
    window.scroll({ top: 0, behavior: "smooth" });
  };

  const borderStyle = (issue) => {
    if (issue === "critical") {
      return { border: "2px solid red", boxShadow: "0 0 10px red" };
    } else if (issue === "major") {
      return { border: "2px solid orange", boxShadow: "0 0 10px orange" };
    } else {
      return { border: "2px solid green", boxShadow: "0 0 10px green" };
    }
  };

  const displayData = show ? fillData : jiraData;

  const handleLoad = () => {
    setCount((count) => count + 4);
  };

  return (
    <React.Fragment>
      <div className="main-container">
        {jiraData &&
          displayData
            .slice(0, count)
            .sort((a, b) => new Date(b.date) - new Date(a.date))
            .map((d) => {
              return (
                <div
                  style={borderStyle(d.prority.toLowerCase())}
                  key={d.id}
                  className="display-card"
                >
                  <h4>
                    {d.ticket.slice(-9)} - {moment(d.date).format("ll")}
                  </h4>
                  <a href={d.ticket}>
                    <h4>{d.ticket}</h4>
                  </a>
                  <p>{d.remarks}</p>
                  <b>{d.prority}</b>
                  <br />
                  <br />
                  <b>Report To:-</b> {d.reporter}
                  <div className="delete-btn">
                    <button onClick={() => handleDelete(d.id)}>Delete</button>
                    <button onClick={() => handleUpdate(d.id)}>Update</button>
                  </div>
                </div>
              );
            })}
      </div>
      {jiraData.length < count ? "" : <Loadmore handleLoad={handleLoad} />}
    </React.Fragment>
  );
};

export default JiraDisplay;
